.z-message{position:absolute;z-index:1000}.z-message>.z-message-pop{max-width:100px;background:#fff;overflow:hidden;-webkit-transform:rotate(0);-ms-transform:rotate(0);transform:rotate(0);box-shadow:0 8px 10px 1px rgba(0,0,0,0.14),0 3px 14px 2px rgba(0,0,0,0.12),0 5px 5px -3px rgba(0,0,0,0.2);padding:12px;border-radius:3px}.z-message-align-center{text-align:center}.z-message-align-right{text-align:right}
