.z-bubble-slot .z-bubble-text{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.z-bubble{position:absolute;left:0;top:0;z-index:999;background:#fff;box-shadow:0 8px 10px 1px rgba(0,0,0,0.14),0 3px 14px 2px rgba(0,0,0,0.12),0 5px 5px -3px rgba(0,0,0,0.2);border-radius:3px;padding:8px}.z-bubble.z-bubble-fixed{position:fixed}.z-bubble-slot{max-width:300px}.z-bubble-slot .z-bubble-text{width:100%;display:inline-block}.z-bubble.z-bubble-custom .z-bubble-slot{max-width:none}.z-bubble .z-bubble-arrow{position:absolute;top:-10px;left:0;right:0;height:15px;width:16px;margin:auto;overflow:hidden;-webkit-transform:rotate(0);-ms-transform:rotate(0);transform:rotate(0)}.z-bubble .z-bubble-arrow .z-icon{position:absolute;top:0;left:0}.z-bubble .z-bubble-arrow .z-icon .z-icon-triangle-up{font-size:18px}.z-bubble .z-bubble-arrow .z-bubble-border{top:-1px;color:#d6d6d6}.z-bubble .z-bubble-arrow .z-bubble-body{color:#fff}
