.z-upload-pick-plus,.z-upload-preview,.z-upload-preview-layover,.z-upload-crop{position:absolute;left:0;right:0;top:0;bottom:0;margin:auto}.z-upload-pick{position:relative;overflow:hidden;cursor:pointer;width:60px;height:60px;border:#d6d6d6 1px solid;margin:0 10px 10px 0;box-sizing:border-box}.z-upload-pick-plus{text-align:center;width:30px;height:30px;font-size:30px}.z-upload-pick-input{position:absolute;cursor:default;opacity:0;width:100%;height:100%;z-index:1}.z-upload-showcase{display:inline-block;vertical-align:middle}.z-upload-showcase-ele{position:relative;width:60px;height:60px;border:#d6d6d6 1px solid;margin:0 10px 10px 0}.z-upload-showcase-delete{position:absolute;top:-10px;right:-10px;cursor:pointer;z-index:1}.z-upload-showcase-delete .fa-times-circle{font-size:18px}.z-upload-showcase-content{overflow:hidden;width:100%;height:100%;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-align-items:center;-ms-flex-align:center;align-items:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center}.z-upload-showcase-content>img{width:100%;display:inline-block;vertical-align:middle}.z-upload-preview{position:fixed;overflow:hidden;width:80%;height:calc(100vh * 80%);padding:10px;box-sizing:border-box;z-index:999;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-align-items:center;-ms-flex-align:center;align-items:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center}.z-upload-preview>img{width:100%;vertical-align:middle}.z-upload-preview-layover{background-color:rgba(0,0,0,0.6);position:fixed;z-index:-1}.z-upload-preview-close{position:absolute;bottom:30px}.z-upload-preview-delete{position:absolute;right:20px;top:20px}.z-upload-hint{display:inline-block;vertical-align:middle;max-width:150px;margin-left:8px}.z-upload-crop{position:fixed;width:100%;height:100vh;z-index:999}.z-upload-crop-operation{position:absolute;bottom:10px;left:0;width:100%;box-sizing:border-box;padding:10px}@media only screen and (max-width: 575px){.z-upload-crop{width:100%;height:100vh}}
