.z-crop-layover,.z-crop-main{position:absolute;left:0;right:0;top:0;bottom:0;margin:auto}.z-crop{position:relative;z-index:0}.z-crop-layover{background-color:rgba(0,0,0,0.6);position:fixed}.z-crop-main{width:80%;height:calc(100vh * 0.8);position:fixed;z-index:999}@media only screen and (max-width: 575px){.z-crop-main{width:100%;height:100vh}}
