.z-menu{display:inline-block;position:relative;box-sizing:border-box;background-color:#fff;cursor:default;line-height:0}.z-menu>.z-menu-ban{position:absolute;width:100%;height:100%;z-index:5;opacity:0}.z-menu>.z-menu-trigger{position:relative;display:inline-block;line-height:0}.z-menu>.z-menu-panel{position:absolute;top:0;left:0;overflow:hidden;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2);border-bottom-right-radius:3px;border-bottom-left-radius:3px;line-height:1;z-index:2;transition:top 300ms ease, height 300ms ease;will-change:top, height}.z-menu>.z-menu-panel .z-menu-container>.z-menu-ele{background-color:#fff;padding:8px}.z-menu.z-menu-ui-material.z-menu-theme-primary{vertical-align:middle;border-radius:3px}@media only screen and (max-width: 767px){.z-menu.z-menu-ui-material.z-menu-theme-primary{width:100%}}
